.back {
  display: flex;
  column-gap: 5px;
  align-items: center;
  font-size: 20px;
  border-bottom: 2px solid #626262;
  letter-spacing: 1px;
  color: #545454;
  font-weight: bold;
  transition: all 400ms ease;
}
.back:hover {
  color: rgba(1, 91, 12, 0.91);
  border-bottom: 2px solid rgba(1, 91, 12, 0.91);
  cursor: pointer;
}

