.cardEmployee {
  background-color: white;
  width: 30%;
  display: flex;
  flex-direction: column;
  box-sizing: border-box;
  row-gap: 5px;
  transition: all 700ms ease;
  border-radius: 20px;
  overflow: hidden;
  height: 450px;
  box-shadow: 0px 0px 20px 1px inset rgba(148, 148, 148, 0.89);
  padding-bottom: 20px;
}
@media (max-width: 1300px) {
  .cardEmployee {
    width: 45%;
  }
}
@media (max-width: 850px) {
  .cardEmployee {
    max-width: 300px;
    width: 100%;
  }
}
.cardEmployee:hover {
  cursor: pointer;
  opacity: 0.9;
}
.cardEmployee .containerForImages {
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
  height: 60%;
  overflow: hidden;
  box-sizing: border-box;
}
.cardEmployee .containerForImages.darker img {
  filter: brightness(0.9);
}
.cardEmployee .containerForImages.darker .background {
  filter: brightness(0.8);
}
.cardEmployee .containerForImages img {
  height: 100%;
  z-index: 15;
  position: relative;
}
.cardEmployee .containerForImages .background {
  display: block;
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  z-index: 10;
  filter: blur(40px);
  border-radius: 20px;
}
.cardEmployee .containerForInformation {
  width: 100%;
  height: 40%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
  padding-bottom: 15px;
  box-sizing: border-box;
}
.cardEmployee .containerForInformation .describe {
  width: 85%;
  text-align: center;
}

