.container {
  width: 100%;
  box-sizing: border-box;
  @media (max-width: 1200px) {
    width: 100%;
  }
  iframe {
    width: 260px;
    height: 180px;
    @media (max-width: 1200px) {
      width: 100%;
      height: 160px;
    }
  }

  .title {
    @media (max-width: 1200px) {
      display: none;
    }
  }
}
