.form {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 20px;
  box-sizing: border-box;
  width: 40%;
  border-radius: 20px;
  row-gap: 10px;

  input:first-of-type {
    width: 200px;
    font-weight: bold;
    background-color: rgba(211, 211, 211, 0.91);
    padding: 4px 8px;
    font-size: 18px;
    color: #2d2d2d;
    border: 1px solid gray;
    border-radius: 5px;
  }
}
