.containerForContent {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  box-sizing: border-box;
}
.containerForContent .content {
  width: 450px;
  text-align: center;
  line-height: 1.4;
  font-size: 19px;
  margin-bottom: 20px;
}

@media (max-width: 750px) {
  .containerForContent .content {
    width: 350px;
    line-height: 1.2;
    margin-bottom: 15px;
  }
}
@media (max-width: 550px) {
  .containerForContent .content {
    width: 80%;
    font-size: 14px;
  }
}

