footer {
  background-color: #2D2D2D;
  box-sizing: border-box;

  .topPartFooter {
    padding: 50px 40px;
    display: flex;
    column-gap: 15px;
    justify-content: space-around;
    @media (max-width: 1200px) {
      padding: 20px 25px;
      flex-wrap: wrap;
      justify-content: flex-start;
      row-gap: 10px;
    }
  }

  .bottomPartFooter {
    border-top: 1px solid rgba(68, 68, 68, 0.59);
    padding: 20px 40px;
    display: flex;
    align-items: center;

    .title {
      color: white;
      letter-spacing: 1px;
      text-shadow: 0px 0px 1px black;

      span {
        color: #ef5151;
      }
    }
  }
}
