.mainTitle {
  text-transform: uppercase;
  margin-bottom: 8px;
  font-size: 25px;
  color: #ef5151;
  letter-spacing: 1px;
  text-shadow: 0px 1px 1px white;
}
@media (max-width: 1100px) {
  .mainTitle {
    font-size: 23px;
    width: 80%;
    text-align: center;
    margin-bottom: 5px;
  }
}
@media (max-width: 700px) {
  .mainTitle {
    font-size: 18px;
  }
}

.describeUnderTitle {
  width: 50%;
  text-align: center;
  color: #464646;
  font-weight: bold;
  font-size: 14px;
  margin-bottom: 15px;
}
@media (max-width: 800px) {
  .describeUnderTitle {
    width: 90%;
  }
}

.containerInRegistration {
  display: flex;
  flex-direction: column;
  align-items: center;
  row-gap: 2px;
  width: 1000px;
  box-sizing: border-box;
}

.flexibleBlock {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
}
.flexibleBlock > div {
  flex-grow: 2;
}

.containerSubPage {
  background-color: #F5F8F9;
  position: relative;
  padding: 40px 10px;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  flex-grow: 10;
}

.describeInInformationCard {
  width: 50%;
  font-size: 18px;
  line-height: 1.4;
  text-shadow: 0px 0px 1px black;
  color: #575757;
  margin-bottom: 10px;
  text-align: justify;
  display: flex;
  justify-content: center;
}
@media (max-width: 1000px) {
  .describeInInformationCard {
    width: 90%;
  }
}
@media (max-width: 800px) {
  .describeInInformationCard {
    font-size: 15px;
  }
}
@media (max-width: 700px) {
  .describeInInformationCard {
    font-size: 14px;
  }
}

.iconMoney {
  color: rgba(117, 140, 47, 0.83);
}

.iconClock {
  color: rgba(65, 65, 63, 0.74);
}

.nextBtn {
  padding: 5px 25px;
  border-radius: 10px;
  color: white;
  background-color: rgba(0, 114, 0, 0.93);
  transition: all 400ms ease;
  font-size: 16px;
  letter-spacing: 1px;
  text-shadow: 0px 1px 1px #676767;
  border: 1px inset #606060;
}
.nextBtn:hover {
  cursor: pointer;
  background-color: rgba(1, 91, 1, 0.93);
}

.btnBook {
  background-color: #014D55;
  color: white;
  height: 40px;
  padding: 5px 20px;
  font-size: 16px;
  border-radius: 20px;
  border: 1px solid #014249;
  transition: 400ms;
}
.btnBook:hover {
  cursor: pointer;
  background-color: #004148;
}

.btnBackToHome {
  color: #fff;
  background: #014d55;
  font-size: 12px;
  padding: 8px 28px;
  border-radius: 35px;
  min-width: 200px;
  text-transform: uppercase;
  border: 2px solid #024850;
  letter-spacing: 1px;
  transition: all 400ms ease;
}
.btnBackToHome:hover {
  cursor: pointer;
  background: #00373d;
}
.btnBackToHome:focus {
  background: #014d56;
}

.nameSurname {
  margin-top: 5px;
  color: black;
  text-align: center;
  text-shadow: 0px 0px 1px black;
  text-transform: uppercase;
  font-size: 18px;
  letter-spacing: 1px;
}
@media (max-width: 950px) {
  .nameSurname {
    font-size: 15px;
    margin-top: 0px;
  }
}
@media (max-width: 650px) {
  .nameSurname {
    font-size: 13px;
  }
}

.titleInFooter {
  margin-bottom: 10px;
  color: white;
  font-size: 20px;
  text-shadow: 0px 0px 1px black;
}
@media (max-width: 1200px) {
  .titleInFooter {
    font-size: 16px;
  }
}

.contentInFooter {
  color: white;
  font-size: 15px;
  line-height: 1.2;
  text-shadow: 0px 0px 1px black;
}
@media (max-width: 1200px) {
  .contentInFooter {
    line-height: 1.1;
    font-size: 14px;
  }
}

.pinkBtn {
  background-color: #ef5151;
  border-radius: 10px;
  text-shadow: 0px 1px 1px black;
  display: flex;
  align-items: center;
  justify-content: center;
  text-transform: uppercase;
  color: white;
  transition: all 500ms ease;
  font-weight: bold;
  font-size: 15px;
  border: none;
  margin-top: 25px;
  padding: 10px 40px;
}
@media (max-width: 1250px) {
  .pinkBtn {
    margin-top: 10px;
    padding: 5px 20px;
    font-size: 14px;
  }
}
@media (max-width: 900px) {
  .pinkBtn {
    padding: 5px 10px;
    font-size: 12px;
  }
}
@media (max-width: 500px) {
  .pinkBtn {
    padding: 2px 5px;
    font-size: 8px;
  }
}
.pinkBtn:hover {
  cursor: pointer;
  background-color: #be3b3b;
}

