.container {
  width: 100%;
  background-color: #eaeaea;
  box-sizing: border-box;
  position: relative;
  padding: 5px 5px 0px 5px;
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: center;
  border-bottom: 2px solid #525252;
  box-shadow: 0px 0px 40px 5px inset rgba(194, 193, 193, 0.95);
}
.container h3 {
  font-size: 19px;
  font-weight: bold;
  color: #5b5b5b;
  text-shadow: 0px 1px 1px #dea1a1;
}
.container span {
  font-size: 22px;
}

