.container {
  width: 100%;
  box-sizing: border-box;
}
@media (max-width: 1200px) {
  .container {
    width: 100%;
  }
}
.container iframe {
  width: 260px;
  height: 180px;
}
@media (max-width: 1200px) {
  .container iframe {
    width: 100%;
    height: 160px;
  }
}
@media (max-width: 1200px) {
  .container .title {
    display: none;
  }
}

