.categoryCard {
  box-sizing: border-box;
}
.categoryCard .titleCategory {
  width: 40%;
  color: white;
  background-color: #ef5151;
  padding: 10px 0;
  margin: 20px 0px 40px 0px;
  display: flex;
  justify-content: center;
  font-size: 18px;
}
@media (max-width: 1100px) {
  .categoryCard .titleCategory {
    margin: 10px 0px 20px 0px;
    padding: 5px 0;
    font-size: 15px;
  }
}

