.header {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
  background-color: black;
  box-sizing: border-box;

  .container {
    width: 100%;
    height: 40vw;
    display: flex;
    justify-content: center;
    flex-direction: column;
    align-items: center;
    background-image: url("../../assets/images/barber.jpg");
    background-repeat: no-repeat;
    background-size: 100vw auto;

    .title {
      color: white;
      width: 600px;
      line-height: 1.1;
      font-size: 40px;
      letter-spacing: 2px;
      text-align: center;
      text-shadow: 0px 0px 1px black;
      margin-bottom: 20px;
      @media (max-width: 1250px) {
        letter-spacing: 1px;
        width: 450px;
      }
      @media (max-width: 1050px) {
        letter-spacing: 0;
        width: 380px;
        font-size: 28px;
        margin-bottom: 5px;
      }
      @media (max-width: 900px) {
        letter-spacing: 0;
        width: 300px;
        font-size: 20px;
      }
      @media (max-width: 800px) {
        font-size: 15px;
      }
      @media (max-width: 800px) {
        width: 110px;
        font-size: 12px;
      }
    }

    .content {
      color: white;
      width: 450px;
      font-size: 20px;
      line-height: 1.4;
      text-align: center;
      @media (max-width: 1050px) {
        width: 290px;
        line-height: 1.1;
        font-size: 18px;
      }
      @media (max-width: 800px) {
        width: 140px;
        font-size: 9px;
      }
      @media (max-width: 400px) {
        width: 100px;
        font-size: 8px;
      }
    }

    .btnBookNow {

    }
  }
}
